@import url("https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap");
:root {
  --bb-nav: #1E1E1E;
  --bb-pink: #FF5C90;
  --bb-pink-light: #EA45A0;
  --bb-green: #32806F;
  --bb-green-light: #00AD45;
  --bb-fluo: #64FF4F;
  --bb-blue: #3B86E9;
  --bb-dark-blue: #3F4C5E;
  --bb-salmon: #FF8A8A;
  --bb-purple: #8955FC;
  --bb-purple-light: #8850FE;
  --bb-grey: #202225;
  --bb-red: #ED2D45;
  --bb-white: #FFFFFF;
  --bb-black: #000000;
}

@font-face {
  font-family: "Dogica";
  src: url(../../fonts/OTF/dogica.otf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "VHSGothic";
  src: url(../../fonts/VHSGhotic/vhs-gothic.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Visitor";
  src: url(../../fonts/Visitor/visitor1.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Retro-Computer";
  src: url(../../fonts/TTF/retro_computer.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.share-tech-mono-regular {
  font-family: "Share Tech Mono", monospace;
  font-weight: 400;
  font-style: normal;
}

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Retro-Computer", sans-serif;
}

body {
  position: relative;
  background: var(--bb-nav);
}

nav {
  position: fixed;
  width: 100%;
  height: 100px;
  z-index: 10;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

.wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  height: 100%;
}

.nav-wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1550px;
  height: 100%;
}

section,
header {
  height: 100vh;
  background: url(../img/backgroundBB.webp);
  background-size: cover;
  background-position: center;
  z-index: 0;
}

.nav-menu {
  height: 100%;
  width: 100%;
}
.nav-menu__container {
  padding: 0 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.nav-menu a,
.nav-menu button,
.nav-menu li {
  cursor: pointer;
}
.nav-menu__mobile {
  display: none;
}
.nav-menu__logo, .nav-menu__others {
  width: 15%;
}
.nav-menu__logo {
  display: flex;
  justify-content: flex-end;
}
.nav-menu__logo img {
  max-width: 125px;
  width: 100%;
  height: 100%;
}
.nav-menu__links {
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
  width: 70%;
  height: 100%;
  text-transform: uppercase;
  font-size: 1rem;
  color: var(--bb-white);
}
.nav-menu__link {
  background: var(--bb-grey);
  transition: background 0.4s;
}
.nav-menu__link:hover {
  background: var(--bb-green);
}
.nav-menu__link, .nav-menu__others-link {
  padding: 1.6rem 2rem;
  border-radius: 1.3rem;
}
.nav-menu__others {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav-menu__others-link {
  background: var(--bb-grey);
  color: var(--bb-pink);
  font-size: 1.2rem;
  font-weight: 600;
  transition: background 0.4s;
}
.nav-menu__others-link:hover {
  background: var(--bb-purple);
  color: var(--bb-white);
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 100vh;
}
header .video__box {
  padding: 0 5rem;
  max-height: 720px;
  max-width: 1550px;
}
header .video__box video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow-clip-margin: content-box;
  overflow: clip;
  border-radius: 3rem;
}

.email {
  position: relative;
  height: 100%;
  z-index: 0;
  background: url(../../dist/img/emailBg.png);
  background-size: cover;
  background-position: center;
  z-index: 0;
}
.email__graphics {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.email__graphics--bar-code {
  background: url(../../dist/img/barCode.svg);
  background-size: 180px;
  background-position: center bottom 5px;
  background-repeat: no-repeat;
  opacity: 0.7;
}
.email__graphics--eye {
  background: url(../../dist/img/eyeEmail.svg);
  background-size: 180px;
  background-position: right 100px bottom 120px;
  background-repeat: no-repeat;
}
.email__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.email__box {
  padding: 10rem 0 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 640px;
  height: 100%;
}
.email__poster {
  z-index: 0;
}
.email__poster img {
  width: 100%;
  height: auto;
  max-width: 548px;
}
.email__title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20%;
  width: 100%;
  z-index: 10;
}
.email__title h2 {
  display: flex;
  align-items: center;
  font-size: 4.2rem;
  color: var(--bb-white);
}
.email__title h2 img {
  padding: 0 2rem;
  width: 100%;
  height: auto;
  max-width: 180px;
}
.email__form {
  padding: 0 3rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
  height: 50%;
  z-index: 10;
}
.email__form h3 {
  font-size: 1.4rem;
  color: var(--bb-white);
}
.email__form form {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
}
.email__form form input[type=text] {
  margin: 1rem 0;
  padding: 2rem;
  height: 60px;
  border-radius: 1.2rem;
  line-height: 0.4rem;
  border: none;
}
.email__form form input[type=text]::placeholder {
  font-family: "VHSGothic", sans-serif;
  font-size: 1.7rem;
  text-transform: uppercase;
  color: var(--bb-pink);
}
.email__form button {
  padding: 2rem 5rem;
  background: url(../../dist/img/btnSendBB.svg);
  background-size: cover;
  border: none;
  max-width: 150px;
  max-height: 71px;
  height: 100%;
  width: 100%;
  transform: scale(1);
  transition: transform 0.4s;
}
.email__form button:hover {
  transform: scale(1.1);
}
.email__agree-box {
  position: relative;
  padding: 2rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
}
.email__agree-box label {
  font-size: 0.9rem;
  color: var(--bb-white);
}
.email__agree-box input[type=checkbox] {
  position: absolute;
  opacity: 0;
  height: 25px;
  width: 22px;
}
.email__agree-box span {
  margin-right: 1rem;
  display: inline-block;
  background-color: white;
  box-sizing: border-box;
  width: 40px;
  height: 25px;
  border-radius: 0.2rem;
}
.email__agree-box span:has(> input:checked) {
  background-color: var(--bb-purple);
}

.footer {
  background: var(--bb-white);
  width: 100%;
  height: 100%;
}
.footer__container {
  padding: 8rem 0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.footer__container a {
  width: 15%;
  font-size: 1.2rem;
}
.footer__logos {
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: 670px;
  width: 100%;
  background: var(--bb-black);
  border-radius: 1.6rem;
}
.footer__logos img {
  width: 100%;
  height: auto;
  max-width: 105px;
}

@media screen and (max-width: 992px) {
  .nav-menu {
    justify-content: flex-end;
  }
  .nav-menu__mobile {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav-menu__mobile button {
    color: var(--bb-white);
    background-color: transparent;
    border: none;
    width: 40px;
    cursor: pointer;
  }
  .nav-menu__logo, .nav-menu__mobile, .nav-menu__others {
    width: 30%;
  }
  .nav-menu__links {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100vw;
    height: 100vh;
    background: white;
  }
}
@media screen and (max-width: 565px) {
  nav {
    height: 60px;
  }
  .nav-menu__mobile button {
    width: 30px;
  }
  .nav-menu__others-link {
    padding: 1rem 1.2rem;
    font-size: 1.1rem;
  }
}